@import 'responsive.scss';

* {
    font-family: Marcellus,sans-serif;
    font-size:15px;
    box-sizing: border-box;
    padding:0;
    margin:0;
}

.container{
    width: 100%;
}

.row {
    display: -webkit-box;
    display: flex;
    flex-wrap: wrap;
}

.menu{
    margin: 10px 10px 0 10px;
    width: 100%;
    padding: 0 40px;
    .logo-img{
        width: 9em;
        float: left;
    }

    .menu-right{
        color:white;
        text-align: right;
        ul{
            display: inline-block;
            line-height: 5rem;
        }
        li{
            float: left;
            list-style-type: none;
            padding: 0 20px;
            color: #fff;
            a{
                color:#fff;
                font-weight: 400;
                text-decoration: none;
                text-transform: uppercase;
            }

            .menu-link{
                position: relative;
            }
              
            .menu-link:after{
                content: '';
                position: absolute;
                width: 0; height: 3px;
                display: block;
                margin-top: -25px;
                right: 0;
                background: #fff;
                transition: width 0.8s ease;
                -webkit-transition: width 0.8s ease;
            }
            
            .menu-link:hover:after{
                width: 100%;
                left: 0;
                background: #fff;
            }
        }
    }
}


.banner {
    position: absolute;
    top:0;
    z-index: -1;
    background-position:center;
    .banner-full {
        width:100%;
